import {
  mapGetters
} from 'vuex'
import loadMore from '@/components/load-more.vue'
export default {
  name: 'CardList',
  props: ['list', 'loading', 'nodata'],
  components: {
    loadMore
  },
  computed: {
    ...mapGetters([
      'winWidth',
      'winHeight',
      'navHeight'
    ]),
  },
  methods: {
    handleClick(item) {
      this.$emit('cardClick', item)
    },
    scrollToBottom(e) {
      // console.log('scroll to bottom', e)
      this.$emit('scrollToBottom')
    },
    scrollToTop(e) {
      // console.log('scroll to top', e)
      this.$emit('scrollToTop')
    },
  },
}
